import React from "react";
import "./Project.css";
import Project1 from "./Porject.jsx";
export default function Project() {
  return (
    <div className="Project">
      <heading>My Recent Project</heading>
      <div className="ProjectDiv">
        {Project1.map((props) => {
          return (
            <div className="Projects">
             
                <h1 className="Name">{props.Name}</h1>
                <h2 className="Base">{props.Base}</h2>
                <img src={props.img}></img>
                <div className="divProject" Div-select="False"></div>
              
            </div>
          );
        })}
      </div>
    </div>
  );
}
